import React from 'react';
import styled from "styled-components";
import { Flex, Box } from "grid-styled";
import Helmet from "react-helmet";
import Img from "gatsby-image";
import Link from "gatsby-link";

import get from "lodash/fp/get";

import { color, column, font, size, sumSize } from "../theme";
import { transparentize } from "../polished";
import { media } from "../styles/mediaQueries";

import FadeWithoutFont from "../FadeWithoutFont";

const Container = styled(Flex).attrs({ column: true })`
  color: ${get("theme.colors.text")};
  align-items: center;
  width: ${column(12)};
`;

const Headline = styled.h1`
  text-align: center;
  font-family: ${font("display")};
  letter-spacing: 1px;
  margin: 0;
  font-size: ${sumSize(3, 1)};
  font-weight: normal;
  margin-top: ${size(3)};
  margin-bottom: ${size(3)};
`

const Video = styled.div`
  border: 1px solid white;
  width: 640px;
  height: 480px;
`

const LunchHeadline = styled.h2`
  margin: ${size(4)} 0 ${sumSize(3, 2)};
  font-size: ${sumSize(2, 1)};
`

const LunchMenu = styled.ul`
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  width: ${column(3)};
`

const LunchOption = styled.li`
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  width: 40%;
  font-size: ${size(2)};
  padding: ${size(3)} 0;
  background: red;
  border-radius: 9px;
`

const Join = () => {
  return (
    <Container>
      <Headline>
        For Makers<br/>
        By Makers
      </Headline>
      <Video />
      <LunchHeadline>
        Let's have lunch.
      </LunchHeadline>
      <LunchMenu>
        <LunchOption>
          Meat
        </LunchOption>
        <LunchOption>
          Veg
        </LunchOption>
      </LunchMenu>
    </Container>
  )
}

export default Join;



